import React from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';

import Details from '../details'

const Main = () => {
  return (
    <main className="main-wrapper">
      <Switch>
        <Route path="/details" component={Details} />
        <Redirect to="/details" />
      </Switch>
    </main>
  )
};

export default Main;