import React from 'react';
import { NavLink } from 'react-router-dom';
import './style.sass';

const Header = () => {
  return (
    <header className="header">
      <div className="container">
        <div className="nav">
          <NavLink to="/" exact >首页</NavLink>
          <NavLink to="/details">排行</NavLink>
          <NavLink to="/login">登录</NavLink>
        </div>
        <div className="user">
          {/*<Avatar size={40} src="https://imgessl.kugou.com/uploadpic/softhead/240/20181010/20181010161310788.jpg" />*/}
        </div>
      </div>
    </header>
  )
}

export default Header