import {
  REQUEST,
  RECEIVE
} from './actions';

export const list = (state = [], action) => {
  switch (action.type) {
    case RECEIVE:
      return [
        ...action.data
      ]
    default:
      return state;
  }
}

export const meta = (
  state = {
    current_page: 1,
    total: 0,
    per_page: 20
  },
  action
) => {
  switch (action.type) {
    case RECEIVE:
      return {
        ...action.meta
      };
    default:
      return state;
  }
};

export const links = (
  state = {},
  action
) => {
  switch (action.type) {
    case RECEIVE:
      return {
        ...action.links
      };
    default:
      return state;
  }
};

export const fetch = (
  state = false,
  action
) => {
  switch (action.type) {
    case REQUEST:
      return true
    case RECEIVE:
      return false;
    default:
      return state
  }
};

export const initial = (state = false, action) => {
  switch (action.type) {
    case RECEIVE:
      return true;
    default:
      return state;
  }
};
