import { connect } from 'react-redux'
import { fetchSongs } from '../../reducers/actions'
import Songs from '../details/Songs'

const mapStateToSongsProps = (state) => {
  return {
    pageMeta: state.songs.meta,
    songs: state.songs.songs,
    loading: state.songs.isFetching,
    isInitial: state.songs.isInitial
  }
};
const mapDispatchSongsProps = (dispatch) => {
  return {
    handleChange(page, limit) {
      dispatch(fetchSongs(page, limit))
    },
    handleSortColumn(sortColumn, sortType) {
      console.log(sortColumn);
      console.log(sortType);
    },
    initData() {
      dispatch(fetchSongs());
    }
  }
};

export default connect(
  mapStateToSongsProps,
  mapDispatchSongsProps
)(Songs)