import { getArtists, getSongs, getUnionArtists } from '../api';

export const REQUEST = 'REQUEST';
export const RECEIVE = 'RECEIVE';

export const requestArtists = () => ({
  type: REQUEST,
  name: 'ARTISTS'
});

export const receiveArtists = ({artists, meta, links}) => ({
  type: RECEIVE,
  name: 'ARTISTS',
  data: artists,
  meta,
  links
});

export const requestSongs = () => ({
  type: REQUEST,
  name: 'SONGS'
});

export const receiveSongs = ({songs, meta, links}) => ({
  type: RECEIVE,
  name: 'SONGS',
  data: songs,
  meta,
  links
});

export const requestUnionArtists = () => ({
  type: REQUEST,
  name: 'UNION_ARTISTS'
});

export const receiveUnionArtists = ({artists, meta}) => ({
  type: RECEIVE,
  name: 'UNION_ARTISTS',
  data: artists,
  meta
})

export const fetchArtists = (page, limit) => {
  return (dispatch) => {
    dispatch(requestArtists());
    const res = page ? getArtists({page, limit}) : getArtists();
    res.then(response => {
      const { artists, meta, links } = response.data.data;
      dispatch(receiveArtists({
        artists,
        meta,
        links
      }));

      if (page) {
        document.documentElement.scrollTop = 0
      }

    }, err => {
      return err;
    });
  }
};

export const fetchSongs = (page, limit) => {
  return (dispatch) => {
    dispatch(requestSongs());
    const res = page ? getSongs({page, limit}) : getSongs();
    res.then(response => {
        const { data: songs, meta, links } = response.data.data;
        
        dispatch(receiveSongs({
          songs,
          meta,
          links
        }));

        if (page) {
          document.documentElement.scrollTop = 0
        }
    }, (err) => {
      return err;
    })
  }
};

export const fetchUnionArtists = (page, limit) => {
  return (dispatch) => {
    dispatch(requestUnionArtists());
    const res = page ? getUnionArtists({page, limit}) : getUnionArtists();
    res.then(response => {
      const { artists, meta } = response.data;

      dispatch(receiveUnionArtists({
        artists,
        meta
      }));

      if (page) {
        document.documentElement.scrollTop = 0
      }

    }, err => {
      return err;
    });
  }
};
