instance.interceptors.response.use(response => {
  return response
}, error => {
  if(error.response) {
    switch (error.response.status) {
      case 401:
          // 401
        return 401;
      default:
        return;
    }
  }
  return Promise.reject(error)
})

// const isDev = process.env.NODE_ENV == 'development'

// const baseUrl = isDev ? '/api/' : process.env.VUE_APP_API_URL
const baseUrl = 'https://api.1234.link/'
const unionUrl = 'https://api.v.cool/music/'

export const getArtists = async (params) => {
  return instance.get(`${baseUrl}artists`, {
    params
  });
}

export const getSongs = async (params) => {
  return instance.get(`${baseUrl}songs`, {
    params
  });
};

export const localLogin = ({email, password}) => { //登录
  return axios({
    url: `${baseUrl}login`,
    method: 'POST',
    headers: {
      Accept: 'application/vnd.august.v1+json'  
    },
    data: {
      email,
      password,
      'password_confirmation': password
    }
  })
};

export const localLogout = (data) => { //登出
  return instance.post(`${baseUrl}logout`, data)
};

export const localReg = (data) => { //注册
  return axios.post(`${baseUrl}register`, data)
};

export const getUnionArtists = async (params) => {
  return axios.get(`${unionUrl}artists`, {
    params
  });
}