import { connect } from 'react-redux'
import { fetchArtists } from '../../reducers/actions'
import Artist from '../details/Artists'

const mapStateToArtistsProps = (state) => {
  return {
    pageMeta: state.artists.meta,
    artists: state.artists.artists,
    loading: state.artists.isFetching,
    isInitial: state.artists.isInitial
  }
};
const mapDispatchArtistsProps = (dispatch) => {
  return {
    handleChange(page, limit) {
      dispatch(fetchArtists(page, limit))
    },
    handleSortColumn(sortColumn, sortType) {
      console.log(sortColumn);
      console.log(sortType);
    },
    initData() {
      dispatch(fetchArtists());
    }
  }
};

export default connect(
  mapStateToArtistsProps,
  mapDispatchArtistsProps
)(Artist)