import React from 'react';
import { Route, Redirect, Switch, NavLink } from 'react-router-dom'

import { Radio, Select, Divider, Input } from 'antd'

import Artists from '../containers/VisibleArtists.js'
import Songs from '../containers/VisibleSongs.js'
import UnionArtists from '../containers/VisibleUnionArtists.js'
import ListMenu from '../../components/ListMenu'

import './style.sass'

const { Search } = Input

const fakeDataB = [
  '07-08至07-14',
  '07-01至07-07',
  '06-24至06-30',
  '06-17至06-23',
  '06-10至06-16',
  '06-03至06-09'
]


const TimeMenu = () => {
  return (
    <div className="time-wapper">
      <span className="time">时间：</span>
      <ListMenu
        items={fakeDataB}
        typeKey="month"
        onItemClick={(obj) => {
          console.log(obj)}
        }
        active
      >
        按日
      </ListMenu>
      <ListMenu
        items={fakeDataB}
        typeKey="month"
        onItemClick={(obj) => {
          console.log(obj)}
        }
      >
        按周
      </ListMenu>
      <ListMenu
        items={fakeDataB}
        typeKey="month"
        onItemClick={(obj) => {
          console.log(obj)}
        }
      >
        按月
      </ListMenu>
    </div>
  )
}

const VisibleLinks = ({ match, ...reset }) => {
  return (
    <div className="NavLink">
      <div>     
        <NavLink to="/details/qq">
          <svg className="icon" aria-hidden="true">
            <use xlinkHref="#icon-QQyinle"></use>
          </svg>
          QQ
        </NavLink>
        <NavLink to="/details/kugou">
          <svg className="icon" aria-hidden="true">
            <use xlinkHref="#icon-kugou"></use>
          </svg>
          酷狗
        </NavLink>
      </div>
      <hr />
      <div className="">
        <NavLink to={`${match.url}/artists`}>歌手</NavLink>
        <NavLink to={`${match.url}/songs`}>歌曲</NavLink>
        <NavLink to={`${match.url}/company`}>公司</NavLink>
      </div>
    </div>
  )
}


const Menu = () => {
  return (
    <div>
      <Select defaultValue="2019-7-10">
        <Select.Option value="2019-7-10">2019-7-10</Select.Option>
      </Select>
      <Divider type="vertical" />
      <Radio.Group defaultValue="a" buttonStyle="solid">
        <Radio.Button value="a">日榜</Radio.Button>
        <Radio.Button value="b">周榜</Radio.Button>
        <Radio.Button value="c">月榜</Radio.Button>
      </Radio.Group>
      <Divider type="vertical" />
      <Search
        placeholder="搜索歌手"
        onSearch={value => console.log(value)}
        style={{ width: 200 }}
      />
    </div>
  )
}

const VisibleWapper = ({
  match
}) => {
  return (
    <div>
      <div className="tabs">
        <div className="container">
          <Switch>
            <Route path={`${match.path}/:type`} component={VisibleLinks} />
          </Switch>

          <hr />
          <Menu />
          <hr />
          <TimeMenu />
        </div>
      </div>
      
      <Switch>
        <Route path={`${match.path}/kugou/artists`} component={Artists} />
        <Route path={`${match.path}/kugou/songs`} component={Songs} />
        <Route path={`${match.path}/qq/artists`} component={UnionArtists} />

        <Route
          path={`${match.path}/qq`}
          render={
            ({ match }) => <Redirect to={`${match.path}/artists`} />
          }
        />

        <Redirect to={`${match.path}/kugou/artists`} />
      </Switch>

    </div>
  );
}

export default VisibleWapper