import React, { useEffect } from 'react';
import { Empty, Pagination } from 'antd';
import { Ranking, LoadingWarpper, AvatarCell } from './Component';

import { Table } from 'rsuite';

import get from 'loadsh/get'

const { Column, HeaderCell, Cell } = Table;

const RankingCell = ({
  rowIndex,
  currentPage,
  pageSize,
  ...props
}) => {
  return (
    <Cell {...props}>
      {Ranking((currentPage * pageSize) - pageSize + (rowIndex + 1))}
    </Cell>
  )
};

const formatNum = (num) => {
  const numString = num.toString();
  return numString.length > 4 ? (num / 10000).toFixed(2).toString() + '万' : num;
};

const NewFans = (rowData) => {
  const newFans = get(rowData, 'fans.ratio.count');
  return (
    <div
      style={(newFans > 0) ? {color: '#45A1F2'} : null}
    >
      {formatNum(newFans)}
    </div>
  );
};

const Artists = ({
  artists,
  loading,
  handleChange,
  handleSortColumn,
  pageMeta,
  initData,
  isInitial
}) => {
    useEffect(() => {
      isInitial || initData();
    }, [isInitial, initData]);
    const { page, total, per_page } = pageMeta
    return (
      <div className="song">
        <Table
          autoHeight
          data={artists}
          onRowClick={data => {
            console.log(data);
          }}
          renderEmpty={() =>(<Empty image={Empty.PRESENTED_IMAGE_SIMPLE} />)}
          renderLoading={e => LoadingWarpper(loading) }
          rowClassName="row"
          rowHeight={70}
          onSortColumn={handleSortColumn}
        >
          <Column align="center" verticalAlign="middle" width={100}>
            <HeaderCell>排名</HeaderCell>
            <RankingCell currentPage={page} pageSize={per_page} />
          </Column>

          <Column align="left" verticalAlign="middle" width={760}>
            <HeaderCell>歌手</HeaderCell>
            <Cell dataKey="name">
              {rowData => <AvatarCell src={get(rowData, 'cover')} text={get(rowData, 'name')} />}
            </Cell>
          </Column>

          <Column align="center" verticalAlign="middle" width={170} sortable >
            <HeaderCell>新增粉丝</HeaderCell>
            <Cell dataKey="fans.ratio.count">
              {rowData => NewFans(rowData)}
            </Cell>
          </Column>

          <Column align="center" verticalAlign="middle" width={170} sortable >
            <HeaderCell>总粉丝</HeaderCell>
            <Cell dataKey="fans.today.count">
              {rowData => formatNum(get(rowData, 'fans.yesterday.count'))}
            </Cell>
          </Column>

        </Table>
        <div className="pagination">
          <Pagination
            showSizeChanger
            showQuickJumper
            showLessItems
            hideOnSinglePage
            defaultCurrent={page}
            defaultPageSize={per_page}
            onChange={handleChange}
            total={total}
            onShowSizeChange={handleChange}
          />
        </div>
      </div>
    );
};

export default Artists;