import React, { useEffect } from 'react';
import { Avatar, Empty, Pagination } from 'antd';
import { Ranking, LoadingWarpper } from './Component';
import get from 'loadsh/get';
import { Table } from 'rsuite';


const { Column, HeaderCell, Cell } = Table;


const fakeData = [
  'http://singerimg.kugou.com/uploadpic/softhead/240/20180517/20180517143446938.jpg',
  'https://imgessl.kugou.com/uploadpic/softhead/240/20181010/20181010161310788.jpg',
  'https://imgessl.kugou.com/uploadpic/softhead/240/20190703/20190703165811160.jpg',
  'https://p3fx.kgimg.com/stdmusic/20151111/20151111174428152608.jpg',
  'https://imgessl.kugou.com/uploadpic/softhead/240/20140118/20140118112603422807.jpg',
  'https://imgessl.kugou.com/uploadpic/softhead/240/20170602/20170602104911895.jpg'
];

const RankingCell = ({
  rowIndex,
  currentPage,
  pageSize,
  ...props
}) => {
  return (
    <Cell {...props}>
      {Ranking((currentPage * pageSize) - pageSize + (rowIndex + 1))}
    </Cell>
  )
};

const TotalIndex = ({ data }) => (
  get(data, 'kugou_index', [])
    .reduce((ac, currentValue) => ac + get(currentValue, 'index', 0), 0)
);

const AvatarCell = ({ rowData, dataKey, ...props}) => {
  return (
    <Cell {...props} style={{padding: 4}} >
      <div className="set">
        <div className="Avatar">
          <Avatar size={40} src={fakeData[Math.floor( Math.random() * 6)]} />
        </div>
        <div className="message">
          {rowData[dataKey]}
        </div>
      </div>
    </Cell>
  )
};

const Songs = ({
  songs,
  loading,
  handleChange,
  handleSortColumn,
  pageMeta,
  initData,
  isInitial
}) => {
    useEffect(() => {
      isInitial || initData();
    }, [isInitial, initData]);
    const { current_page, per_page, total } = pageMeta
    return (
      <div className="song">
        <Table
          autoHeight
          data={songs}
          onRowClick={data => {
            console.log(data);
          }}
          renderEmpty={() =>(<Empty image={Empty.PRESENTED_IMAGE_SIMPLE} />)}
          renderLoading={e => LoadingWarpper(loading) }
          rowClassName="row"
          rowHeight={70}
          onSortColumn={handleSortColumn}
        >
          <Column align="center" verticalAlign="middle" width={100}>
            <HeaderCell>排名</HeaderCell>
            <RankingCell currentPage={current_page} pageSize={per_page} />
          </Column>

          <Column align="left" verticalAlign="middle" width={420}>
            <HeaderCell>歌曲</HeaderCell>
            <AvatarCell dataKey="name" />
          </Column>

          <Column align="center" verticalAlign="middle" width={170}>
            <HeaderCell>歌手</HeaderCell>
            <Cell dataKey="artist.name"></Cell>
          </Column>

          <Column align="center" verticalAlign="middle" width={170}>
            <HeaderCell>专辑名</HeaderCell>
            <Cell dataKey="fans"></Cell>
          </Column>

          <Column align="center" verticalAlign="middle" width={170} sortable>
            <HeaderCell>昨日新增指数</HeaderCell>
            <Cell dataKey="newFans"></Cell>
          </Column>

          <Column align="center" verticalAlign="middle" width={170} sortable>
            <HeaderCell>总指数</HeaderCell>
            <Cell style={{color: '#1da1f2'}} dataKey="index">
              {rowData => <TotalIndex data={rowData} />}
            </Cell>
          </Column>

        </Table>
        <div className="pagination">
          <Pagination
            showSizeChanger
            showQuickJumper
            showLessItems
            hideOnSinglePage
            defaultCurrent={current_page}
            defaultPageSize={per_page}
            onChange={handleChange}
            total={total}
            onShowSizeChange={handleChange}
          />
        </div>
      </div>
    );
};

export default Songs;