import { combineReducers } from 'redux'

import { list, meta, links, fetch, initial } from './public';
import wrapperReducer from './wrapperReducer'

const rootReducer = combineReducers({
  artists: wrapperReducer(list, 'UNION_ARTISTS'),
  meta: wrapperReducer(meta, 'UNION_ARTISTS'),
  links: wrapperReducer(links, 'UNION_ARTISTS'),
  isFetching: wrapperReducer(fetch, 'UNION_ARTISTS'),
  isInitial: wrapperReducer(initial, 'UNION_ARTISTS')
});

export default rootReducer;
