import { combineReducers } from 'redux'

import { list, meta, links, fetch, initial } from './public';
import wrapperReducer from './wrapperReducer'

const rootReducer = combineReducers({
  songs: wrapperReducer(list, 'SONGS'),
  meta: wrapperReducer(meta, 'SONGS'),
  links: wrapperReducer(links, 'SONGS'),
  isFetching: wrapperReducer(fetch, 'SONGS'),
  isInitial: wrapperReducer(initial, 'SONGS')
});

export default rootReducer;