import { connect } from 'react-redux'
import { fetchUnionArtists } from '../../reducers/actions'
import UnionArtist from '../details/UnionArtists'

const mapStateToArtistsProps = (state) => {
  return {
    pageMeta: state.unionArtists.meta,
    artists: state.unionArtists.artists,
    loading: state.unionArtists.isFetching,
    isInitial: state.unionArtists.isInitial
  }
};
const mapDispatchArtistsProps = (dispatch) => {
  return {
    handleChange(page, limit) {
      dispatch(fetchUnionArtists(page, limit))
    },
    handleSortColumn(sortColumn, sortType) {
      console.log(sortColumn);
      console.log(sortType);
    },
    initData() {
      dispatch(fetchUnionArtists());
    }
  }
};

export default connect(
  mapStateToArtistsProps,
  mapDispatchArtistsProps
)(UnionArtist)