import React, { useEffect } from 'react';
import { Avatar, Empty, Pagination } from 'antd';
import { Ranking, LoadingWarpper } from './Component';
import get from 'loadsh/get'
import { Table } from 'rsuite';

const { Column, HeaderCell, Cell } = Table;

const RankingCell = ({
  rowIndex,
  currentPage,
  pageSize,
  ...props
}) => {
  return (
    <Cell {...props}>
      {Ranking((currentPage * pageSize) - pageSize + (rowIndex + 1))}
    </Cell>
  )
};

const AvatarCell = ({ rowData, dataKey, ...props}) => {
  return (
    <Cell {...props} style={{padding: 4}} >
      <div className="set">
        <div className="Avatar">
          <Avatar size={40} src={get(rowData, 'avatar')} />
        </div>
        <div className="message">
          {get(rowData, dataKey)}
        </div>
      </div>
    </Cell>
  )
};

const Artists = ({
  artists,
  loading,
  handleChange,
  handleSortColumn,
  pageMeta,
  initData,
  isInitial
}) => {
    useEffect(() => {
      isInitial || initData();
    }, [isInitial, initData]);
    const { current_page, per_page, total } = pageMeta
    return (
      <div className="song">
        <Table
          autoHeight
          data={artists}
          onRowClick={data => {
            console.log(data);
          }}
          renderEmpty={() =>(<Empty image={Empty.PRESENTED_IMAGE_SIMPLE} />)}
          renderLoading={e => LoadingWarpper(loading) }
          rowClassName="row"
          rowHeight={70}
          onSortColumn={handleSortColumn}
        >
          <Column align="center" verticalAlign="middle" width={100}>
            <HeaderCell>排名</HeaderCell>
            <RankingCell currentPage={current_page} pageSize={per_page} />
          </Column>

          <Column align="left" verticalAlign="middle" width={250}>
            <HeaderCell>歌手</HeaderCell>
            <AvatarCell dataKey="name" />
          </Column>

          <Column align="center" verticalAlign="middle" width={170} >
            <HeaderCell>歌曲</HeaderCell>
            <Cell></Cell>
          </Column>

          <Column align="center" verticalAlign="middle" width={170} sortable >
            <HeaderCell>粉丝</HeaderCell>
            <Cell dataKey="fans"></Cell>
          </Column>

          <Column align="center" verticalAlign="middle" width={170} sortable >
            <HeaderCell>新增粉丝</HeaderCell>
            <Cell dataKey="newFans"></Cell>
          </Column>

          <Column align="center" verticalAlign="middle" width={170} sortable >
            <HeaderCell>指数</HeaderCell>
            <Cell style={{color: '#1da1f2'}} dataKey="index"></Cell>
          </Column>

          <Column align="center" verticalAlign="middle" width={170} sortable >
            <HeaderCell>新增指数</HeaderCell>
            <Cell dataKey="newIndex"></Cell>
          </Column>
        </Table>
        <div className="pagination">
          <Pagination
            showSizeChanger
            showQuickJumper
            showLessItems
            hideOnSinglePage
            defaultCurrent={current_page}
            defaultPageSize={per_page}
            onChange={handleChange}
            total={total}
            onShowSizeChange={handleChange}
          />
        </div>
      </div>
    );
};

export default Artists;