import React from 'react'
import { Icon } from 'rsuite'
import './style.sass'

const Title = (props) => {
  return (
    <span
      className={
        props.active ?
          'title on' :
          'title'
      }
    >
      {props.children}
    </span>
  )
}

const TimeList = (props) => {
  const { items, children, onItemClick, typeKey } = props
  return (
    <div
      className="list-item-wapper"
    >
      <div className="list-item">
        <Title>{children}</Title>
        <ul>
          {items.map((item, index) => (
            <li
              key={index}
              onClick={() => onItemClick({index, item, typeKey})}
            >
              {item}
            </li>
          ))}
        </ul>
      </div>
    </div>
  )
}

const TimeSelector = (props) => {
  const { items, children, active } = props
  return (
    <div className="ListMenu">
      <Title
        active={active}
      >
        {`${children} ${items[0] || null} `}
        <Icon icon="angle-down" />
      </Title>
      <TimeList {...props} />
    </div>
  )
}

export default TimeSelector