import React from 'react';
import { Spin, Avatar } from 'antd';
import './iconfont.js';

export const Ranking = (i) => {
  const _Icon = (type) => {
    return (
      <div>
        <svg className="icon" aria-hidden="true">
          <use xlinkHref={type}></use>
        </svg>
      </div>
    )
  };
  switch(i) {
    case 1:
      return _Icon('#icon-diyiming');
    case 2:
      return _Icon('#icon-dierming1');
    case 3:
      return _Icon('#icon-disanming');
    default:
      return i;
  }
};

export const LoadingWarpper = (isSpin) => {
  return isSpin && (
    <div className="loader-wrapper">
      <div className="loader">
        <Spin />
      </div>
    </div>
  )
};

export const AvatarCell = ({src, text}) => {
  return (
    <div className="set">
      <div className="Avatar">
        <Avatar size={40} src={src} />
      </div>
      <div className="message">
        {text}
      </div>
    </div>
  )
};

export default { Ranking, LoadingWarpper, AvatarCell };